import {Component, trigger, style, transition, animate, state} from '@angular/core';
// import {$$NoteDirec} from '../common/directive/mNote.direc';
import {GLOBAL} from '../common/global';

declare var console: any, mu: any;

// import { Header } from '../layout';


/**
 * Component (matadata)
 *
 * selector    string    
 * inputs    string[]    
 * outputs    string[]    
 * host    {[key: string]: string;}    /
 * providers    any[]     ContentChildren
 * exportAs    string    
 * moduleId    string     id
 * queries    {[key: string]: any;}    
 * viewProviders    any[]     ContentChildren
 * changeDetection    ChangeDetectionStrategy    
 * templateUrl    string    
 * template    string    
 * styleUrls    string[]    
 * styles    string[]    
 * animations    AnimationEntryMetadata[]
 * directives    Array    
 * pipes    Array    
 * encapsulation    ViewEncapsulation    
 * interpolation    [string, string]
 */

@Component({
    selector: 'app',
    templateUrl: 'views/layout/layout.html',
    animations: [
        trigger('mnote', [
            state('hide', style({
                display: 'none'
            })),

            state('show', style({
                display: 'none'
            })),

            transition('hide => show', [
                animate('500ms 1500ms ease-in', style({
                    'background-color': 'rgba(18, 61, 64, .8)',
                    top: '-100px'
                }))
            ])
        ])
    ]
})

export class AppCpt {
    constructor(private G: GLOBAL) {
    }

}



